'use client';

import { services } from '@/data/services';
import * as LucideIcons from 'lucide-react';
import { motion } from 'framer-motion';

const ServicesList = () => {
    // Helper to get icon safely
    const getIcon = (iconName: string) => {
        // @ts-ignore - Dynamic access to library
        const Icon = LucideIcons[iconName];
        return Icon ? <Icon size={32} /> : <LucideIcons.Box size={32} />;
    };

    // Card colors mapping for "fun" look
    const cardColors = [
        "from-purple-500/20 to-blue-500/20 border-purple-500/30 hover:shadow-purple-500/20",
        "from-pink-500/20 to-rose-500/20 border-pink-500/30 hover:shadow-pink-500/20",
        "from-orange-500/20 to-amber-500/20 border-orange-500/30 hover:shadow-orange-500/20",
        "from-cyan-500/20 to-teal-500/20 border-cyan-500/30 hover:shadow-cyan-500/20",
        "from-emerald-500/20 to-green-500/20 border-emerald-500/30 hover:shadow-emerald-500/20",
    ];

    return (
        <section className="py-24 bg-[#050511] text-white relative overflow-hidden">
            {/* Background Vector */}
            <div className="absolute inset-0 z-0">
                <div className="absolute inset-0 bg-[url('/vector-bg.png')] bg-cover bg-center opacity-20 mix-blend-screen" />
                <div className="absolute inset-0 bg-gradient-to-b from-[#050511] via-transparent to-[#050511]" />
            </div>

            <div className="container mx-auto px-4 relative z-10">
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {services.map((service, index) => (
                        <motion.div
                            key={service.id}
                            className={`p-8 rounded-3xl bg-gradient-to-br ${cardColors[index % cardColors.length]} border backdrop-blur-sm transition-all duration-300 group`}
                            whileHover={{ y: -10, scale: 1.02 }}
                            initial={{ opacity: 0, y: 30 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: index * 0.1 }}
                        >
                            <div className="w-16 h-16 rounded-2xl bg-white/10 flex items-center justify-center mb-6 text-white group-hover:scale-110 group-hover:bg-white/20 transition-all duration-300 shadow-[0_0_15px_rgba(255,255,255,0.1)]">
                                {getIcon(service.iconName)}
                            </div>
                            <h3 className="text-2xl font-bold mb-3 text-white group-hover:text-transparent group-hover:bg-clip-text group-hover:bg-gradient-to-r group-hover:from-white group-hover:to-gray-300 transition-colors">{service.title}</h3>
                            <p className="text-gray-400 leading-relaxed text-base">{service.description}</p>
                        </motion.div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default ServicesList;
